import PressSwitch from '../../../packages/press-switch/press-switch.vue';
import { getHideHeaderMixin } from '../../../utils/mixin/hide-header';


export default {
  i18n: {
    'zh-CN': {
      show: '显示',
      hideHead: '隐藏',
      showHead: '显示',
    },
    'en-US': {
      show: 'Show',
      hideHead: 'Hide',
      showHead: 'Show',
    },
