  components: {
    PressSwitch,
  },
  mixins: [
    getHideHeaderMixin(),
  ],
  props: {
    customStyleStr: {
      type: String,
      default: '',
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    showBasicUsage: {
      type: Boolean,
      default: true,
    },
    show: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
    };
  },
  methods: {
    onCustom() {
      this.$emit('onCustom');
    },
    onShowDialog() {
      this.$emit('update:show', true);
    },
